/* iPod Body/Frame */
.IpodFrame {
	min-height: 500px;
	min-width: 300px;
	border-radius: 20px;
	box-shadow: -2px -2px 10px rgba(0, 0, 0, 0.25) inset, 2px 2px 10px white inset;
	position: relative;
	display: flex;
	justify-content: center;
	background: #c8c8ca;
	box-shadow: inset 0 0 20px #555;
}
